.c-charities-carousel {
  background: $s-color--background-subdued;

  &__inner {
    padding: 5.3125rem 0 2.875rem;
    overflow: hidden;
  }

  &__title {
    text-align: center;
    margin-bottom: 4.375rem;
    padding: 0 $s-gutter--edge-mobile;
  }

  &__nav {
    display: none;
  }

  &__slider {
    display: flex;
    overflow-x: auto;

    &.is-initialised {
      overflow: hidden;

      .c-charities-carousel__slide {
        width: auto;
        flex-shrink: initial;

        &:not(.is-active) .c-charities-carousel__charity-blob path {
          fill: $s-color--background !important;
        }

        &.is-active {
          overflow: visible;


          .c-charities-carousel__charity-icon {
            filter: invert(94%) sepia(94%) saturate(0%) hue-rotate(256deg) brightness(107%) contrast(108%);
            // Formula found here: https://codepen.io/sosuke/pen/Pjoqqp
          }

          .c-charities-carousel__content {
            opacity: 1;
          }
        }

        .c-charities-carousel__content {
          opacity: 0;
          transition: all 0.3s ease-in-out;
        }
      }

      .c-charities-carousel__description {
        width: 130%;
      }
    }
  }

  &__slide {
    flex-shrink: 0;
    width: 100%;
  }

  &__slide-inner {
    padding: 0 1.25rem;
  }

  &__icon-wrapper {
    width: 12.5rem;
    height: 12.5rem;
    position: relative;
    margin-bottom: 2.8125rem;
    transition: all 0.5s;
  }

  &__charity-blob path {
    transition: all 0.5s;
  }

  &__charity-icon {
    @include t-absolute-center-x-y;
    width: 67%;
  }

  &__total {
    margin-left: 2rem;
    color: $s-color--font-focus-alt;
    margin-bottom: 1rem;
  }

  &__slide-title {
    margin-left: 2rem;
    margin-bottom: 0.9375rem;
  }

  &__description {
    margin-left: 2rem;
    width: 83.5%;
  }

  @include t-media-query($s-small-up) {
    &__inner {
      position: relative;
    }

    &__slider {
      &.is-initialised {
        overflow: visible;

        .c-charities-carousel__slide {
          overflow: visible;

          &:not(.is-active) .c-charities-carousel__charity-blob path {
            fill: $s-color--background !important; //override inline styles set from customizer
            transition: all 0.5s;
          }

          .c-charities-carousel__description {
            width: 100%;
          }

          &.is-active {
            .c-charities-carousel__description {
              width: 100%;
            }
          }

          &.is-next-active .c-charities-carousel__icon-wrapper,
          &.is-prev-active .c-charities-carousel__icon-wrapper {
            transform: scale(0.7) translateY(-38%);
          }
        }
      }
    }

    &__slide-inner {
      display: flex;
      flex-direction: column;
      align-items: center;
      text-align: center;
    }

    &__total,
    &__description {
      margin: 0;
    }

    &__slide-title {
      margin: 0.9375rem 0;
    }

    &__nav {
      @include t-absolute-center-x-y();
      top: 69%;
      display: flex;
      justify-content: space-between;
      width: 48%;
    }

    &__nav-arrow {
      width: 3.75rem;
      height: 3.75rem;
      cursor: pointer;
      padding: 1.375rem;
    }
  }

  @include t-media-query($s-medium-up) {
    &__inner {
      padding: 8rem 0;
      position: relative;
    }

    &__title {
      margin-bottom: 4.0625rem;
    }

    &__slider {
      display: flex;
      overflow: auto;
      position: relative;
      scrollbar-width: none;
      scrollbar-color: $s-color--background-subdued $s-color--background-subdued;

      &::-webkit-scrollbar {
        width: 0;
      }

      &::-webkit-scrollbar-track {
        background: $s-color--background-subdued;
      }

      &::-webkit-scrollbar-thumb {
        background: $s-color--background-subdued;
      }

      &.is-initialised {
        overflow: visible;

        .c-charities-carousel__icon-wrapper {
          width: 90%;
          height: auto;
          opacity: 0;
          transform: translateY(-107%) scale(0.45);
        }

        .c-charities-carousel__content {
          opacity: 0;
          transition: all 0s;
        }

        .c-charities-carousel__description {
          width: 100%;
        }

        .c-charities-carousel__slide {
          &.is-active {
            overflow: visible;

            .c-charities-carousel__description {
              width: 100%;
            }

            .c-charities-carousel__content {
              opacity: 1;
              transition: all 0.5s;
            }
          }

          &.is-active .c-charities-carousel__icon-wrapper {
            opacity: 1;
            transform: translateY(0) scale(1);
          }

          &.is-next-active .c-charities-carousel__icon-wrapper,
          &.is-prev-active .c-charities-carousel__icon-wrapper {
            opacity: 1;
            transform: scale(0.6) translateY(-38%);
          }

          &.is-2-prev-active .c-charities-carousel__icon-wrapper,
          &.is-2-next-active .c-charities-carousel__icon-wrapper {
            opacity: 1;
            transform: scale(0.45) translateY(-107%);
          }
        }
      }
    }

    &__icon-wrapper {
      transition: transform .5s;
      width: 18%;
      height: auto;
      margin-bottom: 2.5rem;
    }

    &__slide {
      overflow: visible;
    }

    &__slide-title {
      margin: 1.25rem 0;
    }

    &__description {
      margin: 0 auto;
      width: 20%;
      font-size: 1.125rem;
    }

    &__content {
      text-align: center;
      transition: all 0s;
    }

    &__charity-icon {
      top: 47%;
      width: 54%;
    }
  }

  @include t-media-query($s-xlarge-up) {
    &__inner {
      padding-top: 12.0625rem;
      padding-bottom: 8.4375rem;
    }

    &__title {
      margin-bottom: 5.625rem;
    }

    &__nav {
      top: 75%;
      width: 28.5%;
    }

    &__nav-arrow {
      padding: 1.125rem;
    }

    &__description {
      width: 77%;
    }

    &__slider.is-initialised &__slide {

      &.is-active .c-charities-carousel__description,
      .c-charities-carousel__description {
        width: 77%;
        margin: 0 auto;
      }

      .c-charities-carousel__icon-wrapper {
        width: 67.5%;
      }

      &.is-next-active .c-charities-carousel__icon-wrapper {
        opacity: 1;
        transform: scale(0.8) translateY(-16%) translateX(11%);
      }

      &.is-prev-active .c-charities-carousel__icon-wrapper {
        opacity: 1;
        transform: scale(0.8) translateY(-16%) translateX(-11%);
      }

      &.is-2-prev-active .c-charities-carousel__icon-wrapper,
      &.is-2-next-active .c-charities-carousel__icon-wrapper {
        opacity: 1;
        transform: scale(0.6) translateY(-74%);
      }


    }
  }
}